const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			categoryList:[],
			subCategoryList:[],
			imgUrl:'',
			page: '1',
			page_size: '10',
			boardId: '',
			categoryActive: 0,
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			},
		}
	},
	props: {
		//主分类激活索引
		defaultActive: {
			type: Number,
			default: 0
		},
		activeTextColor: {
			type: String,
			default: '#333'
		},
		activeBackgroundColor: {
			type: String,
			default: '#ffffff'
		},
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
		this.getNav()
	},
	mounted() {
		
	},
	methods: {
		categoryMainClick(id,index,name){
			this.boardId = id
			this.page = '1'
			this.page_size = '10'
			this.subCategoryList = []
			this.categoryActive = index;
			this.getList()
		},
		categorySubClick(e){
			console.log(e);
			uni.navigateTo({
				url:`/pages/articleDetail?id=${e.id}`
			})
		},
		// 获取导航
		getNav(){
			uni.request({
				url: `${helper.requestUrl}/board/boards`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.categoryList = res.data.data
						this.boardId = res.data.data[0].id
						this.getList()
						console.log(this.boardId)
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		},
		//获取数据
		getList(){
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					board_id: this.boardId,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						 this.subCategoryList = this.subCategoryList.concat(res.data.data)
						 if(res.data.data == 0){
						 	uni.showToast({
						 		title:'暂无更多数据',
						 		icon:"none"
						 	})
						 }
					} else {
						uni.showToast({
							title: res.data.message,
							icon:'none',
							duration:2000
						});
					}
			
				}
			})
		},
		// scroll() {
		// 	this.page ++;
		// 	this.getList()
		// }
		toLowFun() {
			this.page++;
			this.getList()
		}
	}
}
